.App {
    text-align: center;
}

.App-logo {
    animation: App-logo-spin infinite 20s linear;
    height: 40vmin;
    pointer-events: none;
}

.App-header {
    background-color: #f6f7f8;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    font-size: calc(10px + 2vmin);
    color: #393c45;
}

.App-link {
    color: #61dafb;
}

.grid-container {
    padding: 18px;
    display: grid;
    grid-template-columns: 0.3fr 0.2fr 1.3fr;
    color: #393c45;
    grid-template-rows: 1fr;
    height: auto;
    grid-template-areas: "side sideContent main";
}

.side {
    grid-area: side;
}

.sideContent {
    grid-area: sideContent;
    padding: 10px;
}

.main {
    grid-area: main;
    margin: 0 90px;
}

.p {
    margin: 0 0 30px;
}

.row {
    margin: 0 0 30px;
}

.tags {
    float: left;
    padding-top: 10px;
}

.footer-tags {
    margin-bottom: 50px;
    float: none;
    padding-left: 0;
}

.tags .button {
    display: block;
    padding: 8px;
    background-color: #7fffd4;
    margin-left: 5px;
    float: right;
}

.button {
    background: #303030;
    border: none;
    border-radius: 3px;
    color: #fefefe;
    font-size: 14px;
    font-weight: 700;
    padding: 10px 12px;
}

a {
    text-decoration: none;
}

.tags .cpp {
    background-color: #bfd641;
}

.tags .bootstrap {
    background-color: #b565a7;
    color: #fefefe!important;
}

.tags .react {
    background-color: #282c34;
    color: #61dafb!important;
}

.tags .python {
    background-color: #95dee3;
    color: #000!important;
}

.footer-tags .button {
    float: none;
    display: inline-block;
    margin-top: 18px;
}

@keyframes App-logo-spin {
    from {
        transform: rotate(0deg);
    }
    to {
        transform: rotate(360deg);
    }
}